import { useMemo } from 'react';
// routes
import { paths } from 'src/routes/paths';
// components
import SvgColor from 'src/components/svg-color';
import Iconify from 'src/components/iconify';
import { useAuthContext } from 'src/auth/hooks';
import SalesIcon from 'src/assets/images/tag 1.png'
import Purchase from 'src/assets/images/cart (1) 1.png'
import Payment from 'src/assets/images/hand (1) 1.png'
import Receipt from 'src/assets/images/receipt 1.png'
import Expense from 'src/assets/images/expense 1.png'
import Product from 'src/assets/images/inventory-management 1.png'
import Customer from 'src/assets/images/rating 1.png'
import Vendor from 'src/assets/images/provision 1.png'
import User from 'src/assets/images/profile 1.png'
import Offer from 'src/assets/images/discount (1) 1.png'
import Report from 'src/assets/images/report-card 1 (1).png'
import Setting from 'src/assets/images/settings.png'

// ----------------------------------------------------------------------

const icon = (name) => (
  <SvgColor src={`/assets/icons/navbar/${name}.svg`} sx={{ width: 1, height: 1 }} />
  // OR
  // <Iconify icon="fluent:mail-24-filled" />
  // https://icon-sets.iconify.design/solar/
  // https://www.streamlinehq.com/icons
);

const ICONS = {
  job: icon('ic_job'),
  blog: icon('ic_blog'),
  chat: icon('ic_chat'),
  mail: icon('ic_mail'),
  user: icon('ic_user'),
  file: icon('ic_file'),
  lock: icon('ic_lock'),
  tour: icon('ic_tour'),
  order: icon('ic_order'),
  label: icon('ic_label'),
  blank: icon('ic_blank'),
  kanban: icon('ic_kanban'),
  folder: icon('ic_folder'),
  banking: icon('ic_banking'),
  booking: icon('ic_booking'),
  invoice: icon('ic_invoice'),
  product: icon('ic_product'),
  calendar: icon('ic_calendar'),
  disabled: icon('ic_disabled'),
  external: icon('ic_external'),
  menuItem: icon('ic_menu_item'),
  ecommerce: icon('ic_ecommerce'),
  analytics: icon('ic_analytics'),
  dashboard: icon('ic_dashboard'),
  settings: icon('eva:settings-fill'),
};

export function useNavData() {
  const { user } = useAuthContext();

  let isAdmin = false;
  if (user) {
    isAdmin = user.roleId.name === "ADMIN";
  }

  const data = useMemo(() => {
    const menuItems = [
      // OVERVIEW
      // ----------------------------------------------------------------------
      {
        subheader: 'Accounting & Billing',
        items: [
          {
            title: 'Sales', name: 'sales', path: paths.dashboard.root, icon:
              // <img src={SalesIcon} alt='sales' style={{ tintColor: 'black' }} />
              <SvgColor src={SalesIcon} sx={{ width: 1, height: 1 }} />
          },
          {
            title: 'Purchase',
            name: 'purchase',
            path: paths.dashboard.purchase.root,
            icon: <SvgColor src={Purchase} sx={{ width: 1, height: 1 }} />,
            children: [
              { title: 'Purchase Order', path: paths.dashboard.purchase.order },
              { title: 'Purchase Return Order', path: paths.dashboard.purchase.returnOrder },
            ]
          },
          { title: 'Payments', name: 'payment', path: paths.dashboard.payments, icon: <SvgColor src={Payment} sx={{ width: 1, height: 1 }} /> },
          { title: 'Receipts', name: 'receipt', path: paths.dashboard.receipts, icon: <SvgColor src={Receipt} sx={{ width: 1, height: 1 }} /> },
          { title: 'Expenditure', name: 'expenditure', path: paths.dashboard.expenditure, icon: <SvgColor src={Expense} sx={{ width: 1, height: 1 }} /> },
          {
            title: 'Product Master',
            name: 'product',
            path: paths.dashboard.product.root,
            icon: <SvgColor src={Product} sx={{ width: 1, height: 1 }} />,
            children: [
              { title: 'Products', path: paths.dashboard.product.list },
              { title: 'Bar Codes', path: paths.dashboard.product.barcode },
            ]
          },
          { title: 'Vendor Master', name: 'vendor', path: paths.dashboard.vendor, icon: <SvgColor src={Vendor} sx={{ width: 1, height: 1 }} /> },
          { title: 'Customer Master', name: 'customer', path: paths.dashboard.customer, icon: <SvgColor src={Customer} sx={{ width: 1, height: 1 }} /> },
          {
            title: 'Employee Master',
            name: 'employee',
            path: paths.dashboard.employee.root,
            icon: <SvgColor src={Customer} sx={{ width: 1, height: 1 }} />,
            children: [
              { title: 'Employee Master', path: paths.dashboard.employee.list },
              { title: 'Employee Attendance', path: paths.dashboard.employee.barcode },
            ]
          },
          { title: 'User Management', name: 'user', path: paths.dashboard.user, icon: <SvgColor src={User} sx={{ width: 1, height: 1 }} /> },
          // { title: 'Offer Management', name: 'offer', path: paths.dashboard.offer, icon: <SvgColor src={Offer} sx={{ width: 1, height: 1 }} /> },
          {
            title: 'Reports',
            name: 'reports',
            path: paths.dashboard.reports.root,
            icon: <SvgColor src={Report} sx={{ width: 1, height: 1 }} />,
            children: [
              { title: 'Day Book', path: paths.dashboard.reports.dayBook },
              { title: 'Sales Report', path: paths.dashboard.reports.salesReport },
              { title: 'Purchase Report', path: paths.dashboard.reports.purchaseReport },
              { title: 'Purchase Return Report', path: paths.dashboard.reports.purchaseReturnReport },
              { title: 'Profit and Loss Statement', path: paths.dashboard.reports.profitAndLossStatement },
              { title: 'Ledger', path: paths.dashboard.reports.ledger },
            ]
          },
          {
            title: 'Settings',
            name: 'settings',
            path: paths.dashboard.settings.root,
            icon: <SvgColor src={Setting} sx={{ width: 1, height: 1 }} />,
            children: [
              { title: 'User Profile', path: paths.dashboard.settings.userProfile },
              { title: 'Bank Details', path: paths.dashboard.settings.bankingDetails },
            ]
          },

        ],
      },
    ];
    // Filter menu items based on user permissions
    const filteredMenuItems = user ? menuItems.map(section => ({
      ...section,
      items: section.items.filter(item => {
        if (isAdmin) return true; // Admin has access to all items

        // Check if the user has read permission for the item
        const permission = user.permissions[item.name.toLowerCase()];
        return permission && permission.r;
      })
    })) : menuItems;

    return filteredMenuItems;
  }, [user, isAdmin]);

  return data;
}

