import { lazy, Suspense } from 'react';
import { Outlet } from 'react-router-dom';
// auth
import { AuthGuard } from 'src/auth/guard';
// layouts
import DashboardLayout from 'src/layouts/dashboard';
// components
import { LoadingScreen } from 'src/components/loading-screen';

// ----------------------------------------------------------------------

const SalesPage = lazy(() => import('src/pages/dashboard/one'));
const PayementsPage = lazy(() => import('src/pages/dashboard/two'));
const ReceiptsPage = lazy(() => import('src/pages/dashboard/three'));
const ExpenditurePage = lazy(() => import('src/pages/dashboard/four'));
const PurchaseOrderPage = lazy(() => import('src/pages/dashboard/five'));
const PurcahseReturnOrderPage = lazy(() => import('src/pages/dashboard/six'));
const ProductPage = lazy(() => import('src/pages/dashboard/product/order'));
const ProductBarcodePage = lazy(() => import('src/pages/dashboard/product/barcode'));
const VendorPage = lazy(() => import('src/pages/dashboard/vendorPage'));
const CustomerPage = lazy(() => import('src/pages/dashboard/customerPage'));
const UserPage = lazy(() => import('src/pages/dashboard/userPage'));
const UserDetailsPage = lazy(() => import('src/pages/dashboard/userDetailsPage'));
const OffersPage = lazy(() => import('src/pages/dashboard/offersPage'));
const EmployeeAttendancePage = lazy(() => import('src/pages/dashboard/employee/attendance'));
const EmployeePage = lazy(() => import('src/pages/dashboard/employee/list'));
const EmployeeDetailsPage = lazy(() => import('src/pages/dashboard/employee/details'));
const UserProfilePage = lazy(() => import('src/pages/dashboard/settings/profilePage'));
const UserBankingDetails = lazy(() => import('src/pages/dashboard/settings/bankingDetails'));

// REPORTS
const DayBook = lazy(() => import('src/sections/reports-management/dayBook'));
const SalesReport = lazy(() => import('src/sections/reports-management/salesReport'));
const PurchaseReport = lazy(() => import('src/sections/reports-management/purchaseReport'));
const PurchaseReturnReport = lazy(() => import('src/sections/reports-management/purchaseReturnReport'));
const ProfitAndLossReport = lazy(() => import('src/sections/reports-management/profitAndLoss'));
const LedgerPage = lazy(() => import('src/sections/reports-management/ledger'));
const LedgerDetailPage = lazy(() => import('src/sections/reports-management/ledgerDetails'));

// ----------------------------------------------------------------------

export const dashboardRoutes = [
  {
    path: 'dashboard',
    element: (
      <AuthGuard>
        <DashboardLayout>
          <Suspense fallback={<LoadingScreen />}>
            <Outlet />
          </Suspense>
        </DashboardLayout>
      </AuthGuard>
    ),
    children: [
      { element: <SalesPage />, index: true },
      {
        path: 'purchase',
        children: [
          { path: 'order', element: <PurchaseOrderPage /> },
          { path: 'returnOrder', element: <PurcahseReturnOrderPage /> },
        ],
      },
      { path: 'payments', element: <PayementsPage /> },
      { path: 'receipts', element: <ReceiptsPage /> },
      { path: 'expenditure', element: <ExpenditurePage /> },
      {
        path: 'product',
        children: [
          { path: 'list', element: <ProductPage /> },
          { path: 'barcode', element: <ProductBarcodePage /> },
        ],
      },
      {
        path: 'employee',
        children: [
          { path: 'list', element: <EmployeePage /> },
          { path: 'attendance', element: <EmployeeAttendancePage /> },
          { path: 'details', element: <EmployeeDetailsPage /> },
        ],
      },
      {
        path: 'settings',
        children: [
          { path: 'user-profile', element: <UserProfilePage /> },
          { path: 'user-banking-details', element: <UserBankingDetails /> },
        ],
      },
      {
        path: 'reports',
        children: [
          { path: 'dayBook', element: <DayBook /> },
          { path: 'salesReport', element: <SalesReport /> },
          { path: 'purchaseReport', element: <PurchaseReport /> },
          { path: 'purchaseReturnReport', element: <PurchaseReturnReport /> },
          { path: 'profitAndLossStatement', element: <ProfitAndLossReport /> },
          { path: 'ledger', element: <LedgerPage /> },
          { path: 'ledger/details', element: <LedgerDetailPage /> },
        ],
      },
      { path: 'vendor', element: <VendorPage /> },
      { path: 'customer', element: <CustomerPage /> },
      { path: 'user', element: <UserPage /> },
      { path: 'user/details', element: <UserDetailsPage /> },
      { path: 'offer', element: <OffersPage /> },
    ],
  },
];

// console.log('all routes',dashboardRoutes);